<template>
    <div>

        <div v-if="page_loader" class="loading">
            <div class="alert alert-dismissible alert-info" role="alert">
                <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                Accessing Page...
            </div>
        </div>

        <div v-if="page_load_status.error" class="error">
            <div class="alert alert-dismissible alert-danger" role="alert">
                {{ page_load_status.message }}
            </div>
        </div>

        <div v-if="page_display">

            <div class="page-header">
                <h2>Manage User - All Users</h2>
            </div>

            <div class="row">
                <side-menu></side-menu>
                <div class="col-md-9">
                    <div class="panel panel-default">

                        <div class="panel-heading">
                            <h3 class="panel-title">View All Users</h3>
                        </div><!-- end .panel-heading -->

                        <div class="panel-body">

                            <form class="form-inline" v-on:submit.prevent="">
                                <div class="form-group">
                                    <label>Search By</label>
                                    <select class="form-control" v-model="query.search_column" :disabled="user_loader" @change="trans_op(query.search_column)">
                                        <option value="id">ID</option>
                                        <option value="username">Username</option>
                                        <option value="email">Email</option>
                                    </select>
                                </div>
                                <div class="form-group">
                                    <select class="form-control" v-model="query.search_operator" :disabled="user_loader">
                                        <option v-for="(value, key) in operators" :value="key">{{value}}</option>
                                    </select>
                                </div>

                                <div class="form-group">
                                    <input type="text" class="form-control"
                                           placeholder="Search"
                                           v-model="query.search_input"
                                           @keyup.enter="fetchInitSearch()" :disabled="user_loader">
                                </div>

                                <div class="form-group">
                                    <div class="btn-group" role="group" aria-label="...">
                                        <button type="button" :disabled="user_loader" @click="changeStatusDisplay(-1)" :class="{'btn' : true, 'btn-primary' : query.status_id === -1, 'btn-default' : query.status_id !== -1}">All</button>
                                        <button type="button" :disabled="user_loader" @click="changeStatusDisplay(0)" :class="{'btn' : true, 'btn-primary' : query.status_id === 0, 'btn-default' : query.status_id !== 0}">Deactivated</button>
                                        <button type="button" :disabled="user_loader" @click="changeStatusDisplay(1)" :class="{'btn' : true, 'btn-primary' : query.status_id === 1, 'btn-default' : query.status_id !== 1}">Active</button>
                                        <button type="button" :disabled="user_loader" @click="changeStatusDisplay(2)" :class="{'btn' : true, 'btn-primary' : query.status_id === 2, 'btn-default' : query.status_id !== 2}">Suspended</button>
                                    </div>
                                </div>

                            </form>

                            <div>&nbsp;</div>

                            <div v-if="user_loader" class="loading">
                                <div class="alert alert-dismissible alert-info" role="alert">
                                    <button class="close" disabled><i class="fa fa-spinner fa-spin pull-right"></i></button>
                                    Loading Users...
                                </div>
                            </div>

                            <div class="table-responsive" v-if="!user_loader">
                                <table class="table table-striped table-condensed table-hover">
                                    <thead>
                                    <tr>
                                        <th v-for="column in columns" @click="toggleOrder(column)">
                                            <span :class="{'label label-default' : column === query.column}">{{ column_transformer(column) }}</span>
                                            <span v-if="column === query.column">
                                            <span v-if="query.direction === 'desc'">&darr;</span>
                                            <span v-else>&uarr;</span>
                                         </span>
                                        </th>
                                    </tr>
                                    </thead>
                                    <tbody>
                                    <tr v-for="row in model.data">
                                        <td>{{ row.id }}</td>
                                        <td><router-link :to="{ name: 'user-profile', params: { id: row.id }}">{{ row.username }}</router-link></td>
                                        <td>{{ row.email }}</td>
                                        <td><span :class="'label label-' + $auth.userGroup(row.user_group_id).class_type">{{ $auth.userGroup(row.user_group_id).name_type }}</span></td>
                                        <td><span :class="'label label-' + $auth.userStatus(row.status_id).class_type">{{ $auth.userStatus(row.status_id).name_type }}</span></td>
                                        <td>{{ row.created_at }}</td>
                                        <td><i class="fa fa-remove"></i></td>
                                    </tr>
                                    <tr v-if="model.data && model.data.length === 0">
                                        <td colspan="6">No results</td>
                                    </tr>
                                    </tbody>
                                    <tfoot>
                                    <tr>
                                        <td colspan="6">

                                            <form class="form-inline" v-on:submit.prevent="">
                                                <div class="form-group">
                                                    <span>Displaying {{model.from}} - {{model.to}} of {{model.total}} rows Current Page: {{ model.current_page }} | </span>
                                                </div>

                                                <div class="form-group">
                                                    <label>Go to page</label>
                                                    <input type="text" v-model="query.page"
                                                           class="form-control"
                                                           @keyup.enter="goToPage()">
                                                </div>
                                                <div class="form-group">
                                                    <button class="btn btn-default" @click="prev()">&laquo;</button>
                                                </div>
                                                <div class="form-group">
                                                    <button class="btn btn-default" @click="next()">&raquo;</button>
                                                </div>
                                            </form>
                                        </td>
                                    </tr>
                                    </tfoot>
                                </table>

                            </div>

                        </div><!-- end .panel-body -->
                    </div><!-- end .panel .panel-default -->
                </div><!-- end .col-md-6 -->
            </div><!-- end .row -->

        </div><!-- end v-if="page_display" -->

    </div>
</template>

<style>
</style>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import {accountUrl, userListUrl, getHeader} from './../../config.js'
import SideMenu from './SideMenu.vue'
export default{
  created () {
    console.log('All User created.')
    if (!this.$store.state.authUserStore.permission.is_admin) {
      this.$router.push({name: 'dashboard'})
    }
    if (!this.$store.state.httpRequestStore.httpRequestMainApp) {
      this.$store.dispatch('setHttpRequestMainApp', true)
      this.$store.dispatch('setHttpRequestMainAppHttpCode', 0)
      this.$store.dispatch('setHttpRequestMainAppMessage', null)
      this.$store.dispatch('setHttpRequestMainAppAccess', false)
      this.fetchUserData()
    }
    this.fetchIndexData()
  },

  components: {
    SideMenu
  },

  data () {
    return {
      httpRequest: {
        process: true,
        httpCode: 0,
        trigger: null,
        message: null
      },
      model: {},
      columns: {},
      query: {
        page: 1,
        column: 'id',
        direction: 'desc',
        per_page: 25,
        search_column: 'id',
        search_operator: 'equal',
        search_input: '',
        status_id: -1
      },
      operators: {
        equal: '=',
        not_equal: '<>',
        less_than: '<',
        greater_than: '>',
        less_than_or_equal_to: '<=',
        greater_than_or_equal_to: '>=',
        in: 'IN'
      }
    }
  },

  computed: {
    page_loader () {
      return this.$store.state.httpRequestStore.httpRequestMainApp
    },
    page_load_status () {
      if (this.$store.state.httpRequestStore.httpRequestMainAppHttpCode === 200 && this.$store.state.authUserStore.permission.manage_user) {
        this.$store.dispatch('setHttpRequestMainAppAccess', true)
      }
      /* if (this.$store.state.httpRequestStore.httpRequestMainAppHttpCode === 200 && !this.$store.state.authUserStore.permission.manage_user) {
        this.$store.dispatch('setHttpRequestMainAppMessage', 'No permission to Manage User.')
        this.$store.dispatch('setHttpRequestMainAppAccess', false)
      } */
      return {
        error: !this.page_loader && !this.$store.state.httpRequestStore.httpRequestMainAppAccess,
        message: this.$store.state.httpRequestStore.httpRequestMainAppMessage
      }
    },
    page_display () {
      return !this.page_loader && this.$store.state.httpRequestStore.httpRequestMainAppAccess
    },

    user_loader () {
      return this.httpRequest.process
    },

    ...mapState({
      authUserStore: state => state.authUserStore,
      httpRequestStore: state => state.httpRequestStore
    })
  },

  methods: {
    fetchUserData () {
      this.$http.get(accountUrl, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        console.log(response)

        this.$store.dispatch('setHttpRequestMainAppHttpCode', response.status)

        this.$store.dispatch('setUserName', response.data.username)
        this.$store.dispatch('setUserEmail', response.data.email)
        this.$store.dispatch('setUserFullname', response.data.fullname)
        this.$store.dispatch('setUserStatus', response.data.status_id)
        this.$store.dispatch('setUserGroup', response.data.user_group_id)
        this.$store.dispatch('setUserReg', response.data.created_at)
        this.$store.dispatch('setUserUpdate', response.data.updated_at)

        var permission = {}
        permission = response.data.permission
        this.$store.dispatch('setUserPermission', permission)

        this.$auth.setOneUserToken('username', response.data.username)

        var debounce = _.debounce(() => {
          this.$store.dispatch('setHttpRequestMainApp', false)
        }, 1000)
        debounce()
      }).catch(error => {
        console.log(error)
        if (error.response) {
          this.$store.dispatch('setHttpRequestMainAppHttpCode', error.response.status)
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push('/login')
          }
          if (error.response.data.message) {
            this.$store.dispatch('setHttpRequestMainAppMessage', error.response.data.message)
          } else {
            this.$store.dispatch('setHttpRequestMainAppMessage', error.response.statusText)
          }
        } else {
          this.$store.dispatch('setHttpRequestMainAppHttpCode', 0)
          this.$store.dispatch('setHttpRequestMainAppMessage', error.message)
        }
        var debounce = _.debounce(() => {
          this.$store.dispatch('setHttpRequestMainApp', false)
        }, 500)
        debounce()
      })
    },

    changeStatusDisplay (statusId) {
      this.query.status_id = statusId
      this.fetchInitSearch()
    },

    trans_op (col) {
      if (col === 'id') {
        this.operators = {}
        this.operators.equal = '='
        this.operators.not_equal = '<>'
        this.operators.less_than = '<'
        this.operators.greater_than = '>'
        this.operators.less_than_or_equal_to = '<='
        this.operators.greater_than_or_equal_to = '>='
        this.operators.in = 'IN'
      } else if (col === 'username' || col === 'email') {
        this.operators = {}
        this.operators.equal = '='
        this.operators.not_equal = '<>'
        this.operators.in = 'IN'
        this.operators.like = 'LIKE'
      }
    },

    column_transformer (col) {
      if (col === 'id') {
        return 'ID'
      }
      if (col === 'username') {
        return 'Username'
      }
      if (col === 'email') {
        return 'Email'
      }
      if (col === 'user_group_id') {
        return 'User Group'
      }
      if (col === 'status_id') {
        return 'Status'
      }
      if (col === 'created_at') {
        return 'Joined Date'
      }
    },

    next () {
      if (this.model.next_page_url) {
        this.query.page++
        this.httpRequest.process = true
        this.fetchIndexData()
      }
    },
    prev () {
      if (this.model.prev_page_url) {
        this.query.page--
        this.httpRequest.process = true
        this.fetchIndexData()
      }
    },
    toggleOrder (column) {
      if (column === this.query.column) {
        // only change direction
        if (this.query.direction === 'desc') {
          this.query.direction = 'asc'
        } else {
          this.query.direction = 'desc'
        }
      } else {
        this.query.column = column
        this.query.direction = 'asc'
      }
      this.httpRequest.process = true
      this.fetchIndexData()
    },
    fetchInitSearch () {
      if (!this.user_loader) {
        this.httpRequest.process = true
        this.query.page = 1
        this.fetchIndexData()
      }
    },
    goToPage () {
      if (!this.user_loader) {
        this.httpRequest.process = true
        this.fetchIndexData()
      }
    },
    fetchIndexData () {
      this.$http.get(`${userListUrl('all')}?column=${this.query.column}&direction=${this.query.direction}&page=${this.query.page}&per_page=${this.query.per_page}&search_column=${this.query.search_column}&search_operator=${this.query.search_operator}&search_input=${this.query.search_input}&status_id=${this.query.status_id}`, {headers: getHeader(this.$auth.getUserToken())}).then(response => {
        this.model = response.data.model
        this.columns = response.data.columns
        console.log(response.data.model)
        var debounce = _.debounce(() => {
          this.httpRequest.process = false
        }, 500)
        debounce()
      }).catch(error => {
        console.log(error.response)
        if (error.response) {
          this.httpRequest.httpCode = error.response.status
          if (error.response.status === 401) {
            this.$auth.destroyUser()
            this.$store.dispatch('clearAuthUser')
            this.$router.push({name: 'login'})
          }
          if (error.response.status === 403) {
            this.$router.push({name: 'dashboard'})
          } else {
            this.httpRequest.message = 'Server Error.'
          }
        } else {
          this.httpRequest.httpCode = 0
          this.httpRequest.message = error.message
        }
        var debounce = _.debounce(() => {
          this.httpRequest.process = false
        }, 500)
        debounce()
      })
    }
  }
}
</script>



